	module.exports = {"data":{"post":{"id":"E:/gatsby-starter-personal-blog/content/posts/2017-09-07--react-under-the-hood-part-v/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-f16ff.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 39.06666666666666%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"Shenzhen, China\"\n        title=\"\"\n        src=\"/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-78f2b.jpg\"\n        srcset=\"/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-dce19.jpg 200w,\n/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-c1413.jpg 400w,\n/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-78f2b.jpg 800w,\n/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-ab4c4.jpg 1200w,\n/static/photo-34475542491_9069464269_o-30049ac8137c77be443ebdf7a81f513d-f16ff.jpg 1500w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<blockquote>\n<p>A look behind the curtain of React Starters like:</p>\n<ul>\n<li><a href=\"https://github.com/facebookincubator/create-react-app\">create-react-app</a></li>\n<li><a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby.js</a></li>\n<li><a href=\"https://github.com/zeit/next.js\">Next.js</a></li>\n<li><a href=\"https://neutrino.js.org\">Neutrino</a></li>\n</ul>\n<p>React is often said to be easy to learn, but impossible to set up in an dev environment. Once you start reading about it, you will be faced by an exhausting amount of choices that you have to make, before you can move on to actual coding. Starter Packages, like the ones named above, give a quick access to the React world. Let’s take a look into that black box now.</p>\n</blockquote>\n<p><a href=\"https://github.com/mpolinowski/react-under-the-hood\">Github</a></p>\n<!-- TOC -->\n<ul>\n<li><a href=\"#05-adding-react-icons\">05 Adding React Icons</a></li>\n</ul>\n<!-- /TOC -->\n<h2 id=\"05-adding-react-icons\"><a href=\"#05-adding-react-icons\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>05 Adding React Icons</h2>\n<p>The <a href=\"http://gorangajic.github.io/react-icons/\">React-Icons</a> module allows you to include popular icons in your React projects. The module can be <a href=\"https://www.npmjs.com/package/react-icons\">installed by npm</a></p>\n<p>React-Icons can be imported to our component:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">import</span> Globe <span class=\"token keyword\">from</span> <span class=\"token string\">'react-icons/lib/go/globe'</span>\n<span class=\"token keyword\">import</span> Landing <span class=\"token keyword\">from</span> <span class=\"token string\">'react-icons/lib/md/flight-land'</span>\n<span class=\"token keyword\">import</span> Heart <span class=\"token keyword\">from</span> <span class=\"token string\">'react-icons/lib/go/heart'</span>\n<span class=\"token keyword\">import</span> Checked <span class=\"token keyword\">from</span> <span class=\"token string\">'react-icons/lib/ti/input-checked'</span></code></pre>\n      </div>\n<p>And simply be added to as a child component:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token operator\">&lt;</span>Globe <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n<span class=\"token operator\">&lt;</span>Landing <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n<span class=\"token operator\">&lt;</span>Heart <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n<span class=\"token operator\">&lt;</span>Checked <span class=\"token operator\">/</span><span class=\"token operator\">></span></code></pre>\n      </div>","fields":{"slug":"/react-under-the-hood-part-v/","prefix":"2017-09-07"},"frontmatter":{"title":"React under the Hood (V)","subTitle":"A look behind the curtain of React Starters","cover":{"childImageSharp":{"resize":{"src":"/static/photo-34475542491_9069464269_o-cover-81898c315ac61178d5e969f397b50d4a-ada8c.jpg"}}}}},"author":{"id":"E:/gatsby-starter-personal-blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mike Polinowski</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/contact/\">contact me</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. :hearts:</p>"},"footnote":{"id":"E:/gatsby-starter-personal-blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>This is a personal Developer Blog</li>\n<li>Ξ</li>\n<li>built by <a href=\"https://github.com/mpolinowski\">Mike Polinowski</a></li>\n<li>Ξ</li>\n<li>with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a></li>\n<li>Ξ</li>\n<li>source code on <a href=\"https://github.com/mpolinowski/mpolinowski.github.io\">GitHub</a></li>\n<li>Ξ</li>\n<li>deliverd by <a href=\"https://pages.github.com\">Github Pages</a></li>\n<li>Ξ</li>\n<li>photos from <a href=\"https://www.flickr.com/people/149680084@N06/\">Flickr</a></li>\n</ul>"}},"pathContext":{"slug":"/react-under-the-hood-part-v/"}}

